	updateDisplay() {
		let rectangleContainer = document.getElementById('rectangle-container');
		

		this.renderRectangles(rectangleContainer);
		
		let tournamentData = this.ui.findOrCreateElement(this.tournamentCol, 'div', 'tournament-data', ['tournament-color']);
		let rankData = this.ui.findOrCreateElement(tournamentData, 'div', 'tournament-rank');
		let newRankText = `Overall Rank: ${this.tournament.rank}`;
		this.ui.updateElementTextContent(rankData, newRankText);
		
		let headbandData = this.ui.findOrCreateElement(tournamentData, 'div', 'tournament-headband');
		this.updateHeadbandData(headbandData);
	}

	startFight(id, triggeredByEvent = false) {
		let fighter = this.ui.gameManager.findObjectById(id);
	
		this.tournament.processFighterDefeat(fighter.id);
	
		if (!triggeredByEvent) {
			// Update startIndex to match the currentFighterIndex
			this.startIndex = this.tournament.currentFighterIndex;
			this.renderRectangles(document.getElementById('rectangle-container'));
		}
	}
	
