		this.lifetimeZoneCompletions = DEC(0);
		this.lifetimeRegionProgressions = DEC(0);
		this.lifetimeWorldProgressions = DEC(0);
		
		this.maxProgressionWorld = DEC(0);
		this.maxProgressionRegion = DEC(0);
		
